import React from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import Plot from 'react-plotly.js';
import { makeStyles } from '@material-ui/styles';
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Divider,
  Button
} from '@material-ui/core';
import { useFetch } from 'helpers';

const useStyles = makeStyles(() => ({
  root: {},
  chartContainer: {
    height: 400,
    position: 'relative'
  },
  actions: {
    justifyContent: 'flex-end'
  }
}));

const OrganizationChart = props => {
  const { className, ...rest } = props;
  const [stargazerOrgData, stargazerOrgLoading] = useFetch(
    `${process.env.REACT_APP_API_SERVER}/repo/70908208/stargazer-orgs`
  );

  const classes = useStyles();

  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >
      <CardHeader
        title="Organizations"
      />
      <Divider />
      <CardContent>
        <div className={classes.chartContainer}>
          {stargazerOrgLoading ? (
            "Loading..."
          ) : (
            <Plot
              data = {[{
                values: Object.values(stargazerOrgData),
                labels: Object.keys(stargazerOrgData),
                hoverinfo: 'label+value',
                textinfo: 'label+percent',
                hole: .4,
                type: 'pie'
              }]}
              layout={{
                margin: {
                  t: 50
                },
                autosize: true,
                showlegend: false
              }}
              useResizeHandler={true}
              style={{
                width: '100%',
                height: '100%',
              }}
            />
          )}
        </div>
      </CardContent>
    </Card>
  );
};

OrganizationChart.propTypes = {
  className: PropTypes.string
};

export default OrganizationChart;
